.limiter a {
  font-size: 14px;
  line-height: 1.7;
  color: #666666;
  margin: 0px;
  transition: all 0.4s;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  -moz-transition: all 0.4s;
}
.limiter a:focus {
  outline: none !important;
}
.limiter a:hover {
  text-decoration: none;
  color: #3cc78f;
}
.limiter p {
  font-family: Poppins-Regular;
  font-size: 14px;
  line-height: 1.7;
  color: #666666;
  margin: 0px;
}
.limiter input {
  outline: none;
  border: none;
}
.limiter input:focus::-webkit-input-placeholder {
  color: transparent;
}
.limiter input:focus:-moz-placeholder {
  color: transparent;
}
.limiter input:focus::-moz-placeholder {
  color: transparent;
}
.limiter input:focus:-ms-input-placeholder {
  color: transparent;
}
.limiter input::-webkit-input-placeholder {
  color: #999999;
}
.limiter input:-moz-placeholder {
  color: #999999;
}
.limiter input::-moz-placeholder {
  color: #999999;
}
.limiter input:-ms-input-placeholder {
  color: #999999;
}
.limiter textarea::-webkit-input-placeholder {
  color: #999999;
}
.limiter textarea:-moz-placeholder {
  color: #999999;
}
.limiter textarea::-moz-placeholder {
  color: #999999;
}
.limiter textarea:-ms-input-placeholder {
  color: #999999;
}
.limiter button {
  outline: none !important;
  border: none;
  background: transparent;
}
.limiter button:hover {
  cursor: pointer;
}
.limiter .txt1 {
  font-family: Poppins-Regular;
  font-size: 13px;
  line-height: 1.5;
  color: #999999;
}
.limiter .txt2 {
  font-family: Poppins-Regular;
  font-size: 13px;
  line-height: 1.5;
  color: #666666;
}
.limiter .limiter {
  width: 100%;
  margin: 0 auto;
}
.limiter .container-login100 {
  width: 100%;
  min-height: 100vh;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 15px;
}
.limiter .wrap-login100 {
  width: 960px;
  background: #fff;
  border-radius: 10px;
  overflow: hidden;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 80px 130px 33px 95px;
  transition: all 0.5s ease-in-out;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
}
.limiter .wrap-login100:hover {
  -webkit-box-shadow: 0px 0px 23px 3px #b8b8ab;
  -moz-box-shadow: 0px 0px 23px 3px #b8b8ab;
  box-shadow: 0px 0px 23px 3px #b8b8ab;
}
.limiter .p-t-12 {
  padding-top: 12px;
}
.limiter .p-t-136 {
  padding-top: 136px;
}
.limiter .oe-login {
  padding-top: 15px;
}
.limiter .login100-pic {
  width: 316px;
}
.limiter .login100-pic img {
  max-width: 100%;
}
.limiter .login100-form {
  width: 290px;
}
.limiter .login100-form .dept_sec select {
  position: relative;
  width: 100%;
  background: #e6e6e6;
  height: 50px;
  border-radius: 25px;
  padding: 0 30px 0 68px;
  outline: none;
  border: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  appearance: none;
}
.limiter .login100-form .oe-warning {
  display: block;
  text-align: center;
  color: red;
  position: relative;
  left: 50%;
  margin: 10px 0;
  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
}
.limiter .login100-form .oe-success {
  color: #3cc78f;
  font-weight: bolder;
}
.limiter .login100-form-title {
  font-family: Poppins-Bold;
  font-size: 35px;
  color: #333333;
  line-height: 1.2;
  text-align: center;
  width: 100%;
  display: block;
  padding-bottom: 20px;
}
.limiter .wrap-input100 {
  position: relative;
  width: 100%;
  z-index: 1;
  margin-bottom: 10px;
}
.limiter .input100 {
  font-size: 15px;
  line-height: 1.5;
  color: #666666;
  display: block;
  width: 100%;
  background: #e6e6e6;
  height: 50px;
  border-radius: 25px;
  padding: 0 30px 0 68px;
}
.limiter .focus-input100 {
  display: block;
  position: absolute;
  border-radius: 25px;
  bottom: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  box-shadow: 0px 0px 0px 0px;
  color: #3cc78f;
}
.limiter .input100:focus + .focus-input100 {
  -webkit-animation: anim-shadow 0.5s ease-in-out forwards;
  animation: anim-shadow 0.5s ease-in-out forwards;
}
@-webkit-keyframes anim-shadow {
  to {
    box-shadow: 0px 0px 70px 25px;
    opacity: 0;
  }
}
@keyframes anim-shadow {
  to {
    box-shadow: 0px 0px 70px 25px;
    opacity: 0;
  }
}
.limiter .symbol-input100 {
  font-size: 15px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  align-items: center;
  position: absolute;
  border-radius: 25px;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding-left: 35px;
  pointer-events: none;
  color: #3cc78f;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  -moz-transition: all 0.4s;
  transition: all 0.4s;
}
.limiter .input100:focus + .focus-input100 + .symbol-input100 {
  color: #57b846;
  padding-left: 28px;
}
.limiter .container-login100-form-btn {
  width: 100%;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 20px;
}
.limiter .login100-form-btn {
  font-size: 20px;
  letter-spacing: 1px;
  line-height: 1.5;
  color: #fff;
  text-transform: uppercase;
  width: 100%;
  height: 50px;
  border-radius: 25px;
  background: #3cc78f;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 25px;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  -moz-transition: all 0.4s;
  transition: all 0.4s;
}
.limiter .login100-form-btn:hover {
  background: transparent;
  color: black;
  border: 1px solid #3cc78f;
}
@media (max-width: 992px) {
  .limiter .wrap-login100 {
    padding: 177px 90px 33px 85px;
  }
  .limiter .login100-pic {
    width: 35%;
  }
  .limiter .login100-form {
    width: 50%;
  }
}
@media (max-width: 768px) {
  .limiter .wrap-login100 {
    padding: 100px 80px 33px 80px;
  }
  .limiter .login100-pic {
    display: none;
  }
  .limiter .login100-form {
    width: 100%;
  }
}
@media (max-width: 576px) {
  .limiter .wrap-login100 {
    padding: 100px 15px 33px 15px;
  }
}